body {
  position: relative;
}

#documentation #content {
  width: 100%;
  display: block;
  position: relative;
  padding-left: 184px;
}

#documentation #content .content-spacer {
  padding-right: 40px;
  padding-left: 40px;
}

#documentation #content h4 {
  margin-bottom: 20px;
}

#documentation #content p {
  margin-bottom: 30px;
}

#documentation #sidebar {
  border-right: 1px solid #e3e3e3;
  display: block;
  position: fixed;
  top: 90px;
  left: 0px;
  padding-bottom: 15px;
  bottom: 0;
  width: 200px;
  z-index: 1;
}

ul.table-of-contents {
  list-style: none;
  padding: 0;
  margin: 20px 0 0 20px;
  font-size: 14px;
}

ul.table-of-contents li {
  margin-bottom: 5px;
}

ul.table-of-contents li.active a {
  color: #333;
  border-left: 2px solid #E32546;
}

ul.table-of-contents li a {
  color: #aaa;
  border-left: 2px solid transparent;
  transition: all 0.5s ease;
  padding-left: 20px;
}

ul.table-of-contents li a:hover {
  color: #333;
  border-left: 2px solid #E32546;
}

ul.table-of-contents li.table-heading {
  padding-left: 0px;
  margin: 0 0 10px;
}

ul.table-of-contents h6 {
  font-size: 16px;
}
